.poster {
    margin: 0;
    height: 100%;
    position: relative;
    
}

.poster img {
    width: 100%;
    height: 100vh;
    -o-object-fit: cover;
       object-fit: cover;
    overflow: hidden;
}

.translucent {
    background-color: rgba(51, 51, 51, 0.75);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-backdrop-filter: blur(12px);
            backdrop-filter: blur(12px);
}


